.clients{
  background-color: @col_back_brands;

  &-title{
    text-transform: uppercase;
    .font_U_b();
    .f28();
    color: @col_blue;
    opacity: .4;
    text-align: center;
    padding-top: 95px;
    padding-bottom: 85px;
  }
  &-items{
    .row-flex();
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 70px;
  }
  &-items__item{
    .col();
    .size(4);
    text-align: center;
  }
}




// media
@media screen and (max-width: 965px){

  .clients{

    &-title{
      .f24();
      padding-top: 75px;
      padding-bottom: 65px;
    }
    &-items{
      padding-bottom: 60px;
    }
  }
}

@media screen and (max-width: 775px){

  .clients{

    &-title{
      .f20();
      padding-top: 50px;
      padding-bottom: 50px;
    }
  }
}

@media screen and (max-width: 600px){

  .clients .clients-title{
    .f18();
    padding-top: 34px;
    padding-bottom: 30px;
  }
  .clients .clients-items__item{
    display: flex;
    justify-content: center;
  }
  .slick-dots li:nth-child(4),
  .slick-dots li:nth-child(5),
  .slick-dots li:last-child{
    display: none;
  }
}