// media
@media screen and (max-width: 1315px){

  .header,
  .settings,
  .clients,
  .registration{
    padding: 0 100px;
  }
}

@media screen and (max-width: 1100px){

  .header,
  .settings,
  .clients,
  .registration{
    padding: 0 50px;
  }
}

@media screen and (max-width: 600px){

  .header,
  .settings,
  .clients,
  .registration{
    padding: 0 30px;
  }
}
