.header {
  background-image: url("../images/bg_photo.jpg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  opacity: 0.969;
  position: relative;
  color: #fff;
}
.header:after {
  background-image: linear-gradient(23deg, #282b44 0%, #3d4480 100%);
  background-position: 50% 50%;
  background-repeat: no-repeat;
  opacity: 0.969;
  position: absolute;
  content: "";
  display: block;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
}
.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 45px;
  padding-bottom: 75px;
}
.header-top__logo {
  display: flex;
  width: 50%;
}
.header-top__button {
  text-transform: uppercase;
  font-family: 'Ubuntu-Bold';
  font-size: 12px;
  color: #393e6b;
  background-color: #fff;
  border-radius: 18px;
  padding: 13px 0 11px 0;
  margin-right: -22px;
}
.header-top__button-reg {
  padding: 13px 22px 11px 23px;
}
.header-top__button:hover,
.header-top__button:active {
  background-color: #ededed;
}
.logo {
  position: relative;
  padding-right: 30px;
  margin-right: 58px;
}
.logo::after {
  position: absolute;
  content: '';
  top: -10px;
  right: 0;
  width: 1px;
  height: 51px;
  background-color: #49657d;
}
.logo-allot {
  color: #60e5d7;
}
.nav-list {
  display: flex;
}
.nav-item {
  text-transform: uppercase;
  font-size: 12px;
  margin-right: 38px;
}
.nav-item:last-child {
  margin-right: 34px;
}
.nav-item:hover,
.nav-item:active {
  color: #7b7fa8;
}
.header-main {
  display: flex;
  align-items: center;
  padding-bottom: 75px;
}
.header-main__item {
  width: 50%;
}
.header-main__item:nth-child(2) {
  margin-right: -22px;
}
.header-main__item-title {
  font-family: 'Ubuntu-Light';
  font-size: 34px;
  margin-bottom: 74px;
}
.header-main__item-btn {
  width: 370px;
  margin-left: -5px;
}
.header-main__item-btn__link {
  font-size: 16px;
  padding: 21px 35px 19px 35px;
  border-radius: 30px;
  background-color: #0facbb;
  margin-left: -5px;
}
.header-main__item-btn__link:hover,
.header-main__item-btn__link:active {
  background-color: #0d9aa8;
}
.burger-btn {
  display: none;
}
.burger-btn:hover {
  cursor: pointer;
}
.burger-btn:hover .icon {
  fill: #333860;
}
.burger-btn:active .icon:active {
  fill: #333860;
}
.view {
  display: block !important;
  transition: 3s;
}
