    align-items: center;
    width: 100%;
    order: 2;
  }
  .header .header-main__item:nth-child(2) {
    margin-right: 0;
    order: 1;
    padding-bottom: 35px;
  }
  .header .header-main__item-title {
    font-size: 22px;
    text-align: center;
    margin-bottom: 0;
    padding-bottom: 38px;
  }
}
