.registration {
  background-image: linear-gradient(12deg, #0f8d8e 0%, #14c3d4 100%);
  opacity: 0.9;
  text-align: center;
}
.registration-title {
  text-transform: uppercase;
  font-family: 'Ubuntu-Bold';
  font-size: 36px;
  color: #fff;
  padding-top: 56px;
  padding-bottom: 33px;
}
.registration-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.form-items {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  justify-content: center;
}
.form-items__item {
  width: calc(100% / 2 - 30px);
  margin-bottom: 21px;
}
.form-items__item:first-child,
.form-items__item:nth-child(3) {
  text-align: end;
  margin-right: 10px;
}
.form-items__item:nth-child(2),
.form-items__item:nth-child(4) {
  text-align: start;
  margin-left: 10px;
}
.item-input {
  padding: 15px 24px;
  border-radius: 20px;
  width: 70%;
}
.item-input__long {
  width: 67.5%;
}
.form-container {
  display: flex;
  font-size: 12px;
  width: calc(90%/2);
  color: #fff;
  padding-top: 45px;
  padding-bottom: 38px;
}
.form-container__text {
  text-align: start;
}
.agreement-text {
  color: #22d6e7;
}
.agreement-text:hover,
.agreement-text:active {
  color: #49657d;
}
.form-btn {
  font-family: 'Ubuntu-Bold';
  text-align: center;
  color: #fff;
  background-color: #393e6b;
  padding-top: 17px;
  padding-bottom: 15px;
  border-radius: 25px;
  width: 235px;
  margin-bottom: 50px;
}
.form-btn__link {
  text-transform: uppercase;
}
.form-btn:hover,
.form-btn:active {
  background-color: #333860;
}
.form-container {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  user-select: none;
}
.form-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.checkmark {
  position: absolute;
  left: 0;
  height: 18px;
  width: 18px;
  border-radius: 5px;
  background-color: #fff;
}
.form-container input:checked ~ .checkmark {
  background-color: #fff;
}
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.form-container input:checked ~ .checkmark:after {
  display: block;
}
.form-container .checkmark:after {
  left: 7px;
  top: 4px;
  width: 5px;
  height: 10px;
  border: solid #047d7f;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
