.registration{
  background-image: linear-gradient( 12deg, rgb(15,141,142) 0%, rgb(20,195,212) 100%);
  opacity: .9;
  text-align: center;

  &-title{
    text-transform: uppercase;
    .font_U_b();
    .f36();
    color: @col_white;
    padding-top: 56px;
    padding-bottom: 33px;
  }
  &-form{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.form-items{
  .row-flex();
  justify-content: center;

  &__item{
    .size(2);
    margin-bottom: 21px;
  }
  &__item:first-child,
  &__item:nth-child(3){
    text-align: end;
    margin-right: 10px;
  }
  &__item:nth-child(2),
  &__item:nth-child(4){
    text-align: start;
    margin-left: 10px;
  }
}
.item-input{
  padding: 15px 24px;
  border-radius: 20px;
  width: 70%;
}
.item-input__long{
  width: 67.5%;
}

// политика конф.
.form-container{
  display: flex;
  .f12();
  width: calc(90%/2);
  color: @col_white;
  padding-top: 45px;
  padding-bottom: 38px;

  &__text{
    text-align: start;
  }
}
.agreement-text{
  color: @col_footer_text-light;
}
.agreement-text:hover,
.agreement-text:active{
  color: @col_light_blue;
}
// кнопка
.form-btn{
  .font_U_b();
  text-align: center;
  color: @col_white;
  background-color: @col_blue;
  padding-top: 17px;
  padding-bottom: 15px;
  border-radius: 25px;
  width: 235px;
  margin-bottom: 50px;

  &__link{
    text-transform: uppercase;
  }
}
.form-btn:hover,
.form-btn:active{
  background-color: @col_btn-hover;
}

// checkbox
.form-container{
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  user-select: none;
}
.form-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;;
}
.checkmark {
  position: absolute;
  left: 0;
  height: 18px;
  width: 18px;
  border-radius: 5px;
  background-color: @col_white;
}
.form-container input:checked ~ .checkmark {
  background-color: #fff;
}
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.form-container input:checked ~ .checkmark:after {
  display: block;
}
.form-container .checkmark:after {
  left: 7px;
  top: 4px;
  width: 5px;
  height: 10px;
  border: solid @col_check;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}



// media
@media screen and (max-width: 775px){

  .registration-title{
    .f28();
    padding-top: 48px;
    padding-bottom: 30px;
  }
  .item-input{
    width: 85%;
  }
  .item-input__long{
    width: 80%;
  }
  .form-container{
    padding-top: 33px;

    &__text{
      .f15();
    }
  }
  .form-btn{
    width: 300px;
  }
}

@media screen and (max-width: 600px){

  .registration-title{
    .f24();
    padding-top: 44px;
    padding-bottom: 26px;
  }
  .form-items .form-items__item{
    width: 100%;
    text-align: center;
    margin-right: 0;
    margin-left: 0;
  }
  .form-items .item-input__long{
    width: 85.5%;
  }
  .form-container{
    width: 90%;
  }
  .registration .form-btn{
    width: 100%;
  }
}