.settings{

  &-title{
    .font_U_li();
    .f34();
    text-align: center;
    color: @col_blue;
    padding-top: 110px;
    padding-bottom: 88px;
  }
}
.settings-items{
  .row-flex();
  padding-bottom: 118px;

  &__item{
    .col();
    .size(4);
    display: flex;
    align-items: baseline;
  }
  &__item-cell{
    .font_U_b();
    .f48();
    color: @col_number;
    opacity: .2;
    margin-right: 10px;
  }
  &__item-text{
    color: @col_blue;
  }
}




// media
@media screen and (max-width: 965px){

  .settings-title{
    padding-top: 80px;
    padding-bottom: 60px;
    .f28();
  }
  .settings-items{
    padding-bottom: 90px;
  }
  .settings-items__item{
    .size(3);
  }
}

@media screen and (max-width: 775px){

  .settings-title{
    padding-top: 50px;
    padding-bottom: 30px;
  }
  .settings-items{
    padding-bottom: 60px;
  }
}

@media screen and (max-width: 600px){

  .settings .settings-title{
    padding-top: 35px;
    padding-bottom: 25px;
    .f22();
  }
  .settings .settings-items{
    padding-bottom: 40px;
  }
  .settings-items .settings-items__item{
    .size(2);
  }
}

@media screen and (max-width: 450px){

  .settings-items .settings-items__item{
    .size(1);
  }
}