/* flex-box */
@offset: 30px;
@offset_one_side: (@offset / 2);

.row-flex(){
  display: flex;
  flex-wrap: wrap;
  margin: 0 -@offset_one_side;
}

.col(){
  box-sizing: border-box;
  margin: @offset_one_side;
}

.size(@n){
  width: calc(100% / @n - @offset);
}