// slick
.slick-dotted.slick-slider {
  margin-bottom: 0;
}

// arrow's
.slick-prev,
.slick-next{
  top: 30%;
}

.slick-prev:before,
.slick-next:before {
  content: '';
  display: block;
  position: absolute;

  top: 50%;
  border: 1px solid @col_arrow;
  opacity: .5;
  width: 15px;
  height: 15px;
  transform: translateY(-50%) rotate(45deg);
}
.slick-prev::before{
  left: 20px;
  border-right: 0;
  border-top: 0;
}
.slick-next::before{
  right: 20px;
  border-left: 1px;
  border-bottom: 1px;
}

.slick-active{
  outline: none;
}

// dott's
.slick-dots{
  position: absolute;
  display: flex;
  bottom: 15%;
  left: 45%;
}
.slick-dots li{
  margin-right: 10px;
}
.slick-dots li button:before {
  content: '';
  background-color: @col_dott;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  opacity: .4;
}
.slick-dots li.slick-active button:before {
  opacity: 1;
  background-color: @col_dott;
}