.header{
  background-image: url("../images/bg_photo.jpg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  opacity: 0.969;
  position: relative;
  color: @col_white;
}
.header:after{
  background-image: linear-gradient( 23deg, rgb(40,43,68) 0%, rgb(61,68,128) 100%);
  background-position: 50% 50%;
  background-repeat: no-repeat;
  opacity: 0.969;
  position: absolute;
  content: "";
  display: block;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
}
.header-top{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 45px;
  padding-bottom: 75px;

  &__logo{
    display: flex;
    width: 50%;
  }
  &__button{
    text-transform: uppercase;
    .font_U_b();
    .f12();
    color: @col_blue;
    background-color: @col_white;
    border-radius: 18px;
    padding: 13px 0 11px 0;
    margin-right: -22px;
  }
  &__button-reg{
    padding: 13px 22px 11px 23px;
  }
}
.header-top__button:hover,
.header-top__button:active{
  background-color: @col_white-hover;
}

.logo{
  position: relative;
  padding-right: 30px;
  margin-right: 58px;
}
.logo::after{
  position: absolute;
  content: '';
  top: -10px;
  right: 0;
  width: 1px;
  height: 51px;
  background-color: @col_light_blue;
}
.logo-allot{
  color: @col_mask;
}
.nav{
  &-list{
    display: flex;
  }
  &-item{
    text-transform: uppercase;
    .f12();
    margin-right: 38px;
  }
  &-item:last-child{
    margin-right: 34px;
  }
}
.nav-item:hover,
.nav-item:active{
  color: @col_footer_text;
}

.header-main{
  display: flex;
  align-items: center;
  padding-bottom: 75px;

  &__item{
    width: 50%;
  }
  &__item:nth-child(2){
    margin-right: -22px;
  }

  &__item-title{
    .font_U_li();
    .f34();
    margin-bottom: 74px;
  }
  // кнопка
  &__item-btn{
    width: 370px;
    margin-left: -5px;
  }
  &__item-btn__link{
    .f16();
    padding: 21px 35px 19px 35px;
    border-radius: 30px;
    background-color: @col_btn;
    margin-left: -5px;
  }
}
.header-main__item-btn__link:hover,
.header-main__item-btn__link:active{
  background-color: @col_h-btn_hover;
}

// бургер
.burger-btn{
  display: none;
}
.burger-btn:hover{
  cursor: pointer;
  .icon{
    fill: @col_btn-hover;
  }
}
.burger-btn:active .icon:active{
  fill: @col_btn-hover;
}
// burger show
.view{
  display: block !important;
  transition: 3s;
}





// media
@media screen and (max-width: 1100px){

  .logo {
    padding-right: 20px;
    margin-right: 30px;
  }
}

@media screen and (max-width: 965px){

  .header-top{
    justify-content: flex-start;
    flex-wrap: wrap;
    padding-bottom: 55px;

    &__logo{
      width: 100%;
      justify-content: center;
      padding-bottom: 25px;
    }
    &__nav{
      display: flex;
      justify-content: center;
      width: 70%;
    }
  }

  .header-main{

    &__item{
      width: 60%;
    }
    &__item-title{
      .f28();
    }
    // кнопка
    &__item-btn{
      width: 350px;
      display: flex;
      text-align: center;
    }
    &__item-btn__link{
      padding: 19px 28px 17px 28px;
    }
  }
}

@media screen and (max-width: 775px){

  .header-main{
    align-items: flex-start;

    &__item-btn{
      width: 100%;
      max-width: 350px;
      display: flex;
      text-align: center;
    }
  }
}

@media screen and (max-width: 600px){

  .header .header-top{
    padding-top: 22px;

    &__logo{
      flex-direction: column;
      padding-bottom: 0;
    }
    &__button{
      display: none;
    }
    .nav-list{
      display: none;
      position: absolute;
      flex-direction: column;
      justify-content: center;
      top: 53px;
      right: 5%;
      background: @col_btn-hover;
      border-radius: 15px;
      padding: 10px 0;
      z-index: 555;
    }
    .nav-item{
      padding: 10px 20px;
      margin-right: 0;
    }
  }
  .logo{
    padding-bottom: 22px;
    padding-right: 0;
    margin-right: 0;

    &-description{
      padding-top: 22px;
    }
  }
  .logo::after{
    top: 53px;
    right: 0;
    left: -30px;
    width: 120%;
    height: 1px;
  }
  .burger-btn{
    display: block;
    position: absolute;
    top: 25px;
    right: 30px;
  }

  .header .header-main{
    flex-direction: column;
    align-items: center;
    padding-bottom: 60px;

    &__item{
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      order: 2;
    }
    &__item:nth-child(2){
      margin-right: 0;
      order: 1;
      padding-bottom: 35px;
    }
    &__item-title{
      .f22();
      text-align: center;
      margin-bottom: 0;
      padding-bottom: 38px;
    }
  }
}

@media screen and (max-width: 450px){

  .header .header-main{
    padding-bottom: 40px;
  }
}